import React, { useState, useEffect } from "react";
import Base from "./Base";
import axios from "axios";
// import Button from "react-bootstrap/Button";
// import Form from "react-bootstrap/Form";
// import Container from "react-bootstrap/Container";
import { Container, Row, Col, Form, Button } from "react-bootstrap";

function Login(props) {
  const [formInput, setFormInput] = useState({});

  // const [formData, setFormData] = useState({});

  const [errors, setErrors] = useState({});

  const [successMessage, setSuccessMessage] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          "http://127.0.0.1/mairie/public/api/login"
        );
        setFormInput(response.data);
        // console.log(response.data);
      } catch (e) {
        console.error("Érreur lors de la récupération des données:", e);
      }
    };
    fetchData();
  }, []);

  // console.log(formInput);

  // const handleChange = (event) => {
  //   const { name, value } = event.target;
  //   setFormData({ ...formData, [name]: value });
  //   // console.log(formData);
  // };

  const handleSubmit = async (event) => {
    event.preventDefault();
    // setFormData({ ...formData, []: value });
    const form = event.target;
    const formData = new FormData(form);

    const formDataObject = {};
    for (const [key, value] of formData.entries()) {
      formDataObject[key] = value;
    }
    // console.log(formDataObject);
    // console.log(formData);

    try {
      const responsePost = await axios.post(
        "http://127.0.0.1/mairie/public/api/login",
        formDataObject
      );

      //   // setErrors(responsePost.data);

      //   // console.log(formData);
      //   // setFormData([{}]);
      //   // console.log(response);
    } catch (e) {}
  };

  // Object.keys(formInput).forEach((key) => {
  //   console.log(key, formInput[key].type);
  // });

  return (
    <Base routes={props.routes}>
      <Container className="">
        <Row>
          <Col></Col>
          <Col xs={6}>
            <Form onSubmit={handleSubmit}>
              {formInput.field &&
                Object.keys(formInput.field).map((key, idx) => (
                  <Form.Group
                    key={idx}
                    className="mb-3"
                    controlId={`formLogin${key + idx}`}
                    // invalid={formInput.field[key] ? true : false}
                  >
                    <Form.Label
                      className={
                        formInput.field[key].type == "hidden" ? "d-none" : ""
                      }
                    >
                      {key}
                    </Form.Label>
                    <Form.Control
                      type={formInput.field[key].type}
                      placeholder={`Enter ${key}`}
                      value={formInput.field[key].value}
                      // onChange={handleChange}
                      name={key}
                    />
                    {/* {errors[key] &&
                      errors[key].map((errorMessage, idx) => (
                        <Form.Control.Feedback key={idx} type="invalid">
                          {errorMessage}
                        </Form.Control.Feedback>
                      ))} */}
                    {formInput.field[key].options.help && (
                      <Form.Text className="text-muted">
                        {formInput.field[key].options.help}
                      </Form.Text>
                    )}
                  </Form.Group>
                ))}
              <Button variant="primary" type="submit">
                Submit
              </Button>
            </Form>
          </Col>
          <Col></Col>
        </Row>
      </Container>
    </Base>
  );
}

export default Login;
