import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import Form from "react-bootstrap/Form";
import { Button } from "react-bootstrap";
import React from "react";

// const links = [{ parent: "", href: "", title: "" }];
// const routes = [
//   {
//     path: "/",
//     element: "",
//     label: "",
//   },
// ];

const styles = {textTransform: "capitalize"};

function MyNavbar(props) {
  return (
    <Navbar
      expand="lg"
      className="sticky-top navbar-dark shadow"
      style={{ backgroundColor: "var(--color-30)" }}
    >
      <Container>
        <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            {props.routes.map((route, idx) => (
              <React.Fragment key={`nav-${idx}`}>
                {!route.parent ? (
                  <NavDropdown
                    key={`drop${idx}`}
                    title={route.label}
                    id="basic-nav-dropdown"
                    style={styles}
                  >
                    {route.children.map((child, idx) => (
                      // <NavDropdown.Divider />
                      <NavDropdown.Item key={`drop${idx}-child${idx}`} href={child.path}>
                        {child.label}
                      </NavDropdown.Item>
                    ))}
                  </NavDropdown>
                ) : (
                  <Nav.Link key={`navlink-${idx}`} href={route.path}>
                    {route.label}
                  </Nav.Link>
                )}
              </React.Fragment>
            ))}
          </Nav>

          <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-light">Search</Button>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default MyNavbar;
